<template>
  <section>
    <div class="navbar-container">
      <div class="container-fluid">
        <div class="navbar-header">
           <span class="page-title">报警规则</span>
        </div>
        <div class="navbar-tool pull-right flex">
            <router-link class="el-button el-button--primary cds-button" :to="{name: '创建报警规则', params: {'type': 'add'}}">创建报警规则</router-link>
        </div>
      </div>
    </div>
    <div class="navbar-container box-shadow">
      <div class="container-fluid ">
          <hr class="normal"/>
          <div class="navbar-header flex">
            <el-select placeholder="产品类型" v-model="productDefault" value-key="type" style="width:130px;min-width:130px;">
                <el-option value="">全部</el-option>
                <el-option v-for="m in product_list" :key="m.type" :label="m.name" :value="m.type">{{m.name}}</el-option>
              </el-select>
            <el-select placeholder="监控项" v-model="metrics_id" value-key="value" style="width:150px;min-width:150px;">
                <el-option value="">全部</el-option>
                <el-option v-for="m in metrice_list" :key="m.value" :label="m.name" :value="m.value">{{m.name}}</el-option>
            </el-select>
            <el-select placeholder="启用状态" v-model="active" style="width:130px;min-width:130px;">
                <el-option value="">全部</el-option>
                <el-option value="1" label="启用"></el-option>
                <el-option value="0" label="禁用"></el-option>
            </el-select>
            <el-input placeholder="请输入规则名称" style="width: 300px;" v-model="keyword">
                  <el-button slot="append" @click="search()"><i class="el-icon-search"></i></el-button>
            </el-input>
          </div>
      </div>
    </div>
    <div class="content">
      <el-table class="table--transparent" :data="list">
          <el-table-column label="规则名称" prop="name"></el-table-column>
          <el-table-column label="状态" prop="status_str"></el-table-column>
          <el-table-column label="启用">
            <template slot-scope="scope">
              <span v-if="scope.row.is_active == 1">已启用</span><span v-else>已禁用</span>
            </template>
          </el-table-column>
          <el-table-column label="监控项" prop="metrics_id_str"></el-table-column>
          <el-table-column label="关联对象">
            <template slot-scope="scope">
                <span v-if="scope.row.scope == 0">
                  <span v-if="scope.row.product_type == 'cloud_vm'">全部云主机</span>
                  <span v-else>全部裸金属</span>
                </span>
                <span v-else class="light pointer" @click="FnGetLinkInstance(scope.row)">查看</span>
            </template>
          </el-table-column>
          <el-table-column label="报警规则">
            <template slot-scope="scope">
              {{scope.row.period_str}} {{scope.row.lasted_period_str}} {{scope.row.metrics_id_str}} {{scope.row.trigger_type_str}} 
              {{scope.row.trigger_symbol}}{{scope.row.value}}{{scope.row.unit}} 则发"{{scope.row.level_str}}"报警
            </template>
          </el-table-column>
          <el-table-column label="产品类型" width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.product_type == 'cloud_vm'">云服务器（CDS）</span><span v-else>裸金属（BMS）</span>
            </template>
          </el-table-column>
          <el-table-column label="通知对象">
            <template slot-scope="scope">
                <router-link
                 class="m-right10 light" v-for="(m, i) in scope.row.group_list" 
                 :to="{name: '通知报警联系组', params: {id: m.id}}"
                 :key="i">{{m.name}}</router-link>
                <!-- <span class="light pointer" @click="FnCheckLinkGroup(scope.row)">查看</span>  -->
            </template>
          </el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <ul class="flex flex-center" style="flex-wrap:wrap;">
                <li>
                  <router-link class="light" :to="{name: '报警规则详情', params: {id: scope.row.id}}">查看</router-link>
                </li>
                <li>
                  <router-link class="light" :to="{name: '规则关联历史', params: {name: scope.row.name}}">报警历史</router-link>
                </li>
                <li>
                  <router-link v-if="scope.row.is_active == 1" class="light" :to="{name: '修改报警规则', params: {type: 'update', id: scope.row.id}}">修改</router-link>
                  <span class="pointer remark" v-else>修改</span>
                </li>
                <li v-if="scope.row.is_active == 1">
                  <span class="light pointer" @click="FnOperate('inactive', scope.row)">禁用</span>
                </li>
                 <li v-if="scope.row.is_active == 0">
                  <span class="light pointer" @click="FnOperate('active', scope.row)">启用</span>
                </li>
                <li>
                  <span class="light pointer" @click="FnOperate('delete', scope.row)">删除</span>
                </li>
              </ul>
            </template>
          </el-table-column>
       </el-table>
       <el-pagination class="pull-right" :background="true" @current-change="handleCurrentChange"
          :current-page="currentPage" :page-size="page_size"
          prev-text="上一页" next-text="下一页"
          layout="prev, pager, next, jumper" :total="list_num"></el-pagination>
    </div>
    <msg-dialog :param="msgParam" :data="operateData" @dialogFn="onDialog"  @close="msgParam.visible = false"></msg-dialog>
    <el-dialog title="实例信息" :visible.sync="checkInstancesParam.visible" class="cds-dialog">
       <div label-width="120" class="b_gray">
        <div class="dialog-body">
          <div class="clearfix"><el-button type="primary" @click="FnSyncInstance" class="cds-button pull-right">同步主机信息</el-button></div>
          <table class="table" style="width:100%;margin-top:10px;">
            <thead>
               <tr>
                  <th>ID</th>
                  <th>名称</th>
              </tr>
            </thead>
            <tbody>
               <tr v-for="(m, i) in instance_list" :key="i">
                  <td>{{m.instance_id}}</td>
                  <td>{{m.name}}</td>
                </tr>
            </tbody>
          </table>
        </div>
       </div>
    </el-dialog>
    <linkGroup-dialog :param="checkParam" :data="checkData" @close="checkParam.visible = false"></linkGroup-dialog>
  </section>
</template>

<script>
import msgDialog from "@/components/alarm/msgDialog";
import linkGroupDialog from "@/components/alarm/linkGroupDialog";
import {
  getRulesList,
  activeRules,
  inactiveRules,
  deleteRules,
  linkInstance,
  getGroupList,
  syncInstance
} from "@/service/alarm/alarm_rules";
import { Message } from "element-ui";
export default {
  components: { msgDialog, linkGroupDialog },
  data() {
    return {
      list: [],
      currentPage: 1,
      page_size: 20,
      list_num: 0,
      msgParam: {
        visible: false
      },
      operateList: {
        inactive: {
          title: "禁用报警规则",
          msg: "确定禁用选择的报警规则！",
          msg2: "禁用报警之后将收不到报警通知。"
        },
        active: {
          title: "启用报警规则",
          msg: "确定启用选择的报警规则！"
        },
        delete: {
          title: "删除报警规则",
          msg: "确定删除选择的报警规则！",
          msg2: "删除后将不能恢复。"
        }
      },
      operateData: null,
      keyword: "",
      checkParam: {
        visible: false,
        title: ""
      },
      checkInstancesParam: {
        visible: false,
        title: ""
      },
      checkData: [],
      instance_list: [],
      selectData: "",
      product_list: [
        { name: "云服务器（CDS）", type: "cloud_vm" },
        { name: "裸金属（BMS）", type: "bms" }
      ],
      productDefault: "",
      metrice_list: [
        { name: "CPU使用率", value: "cpu.usage", unit: "%" },
        { name: "内存使用率", value: "mem.usage", unit: "%" },
        { name: "磁盘使用率", value: "disk.usage", unit: "%" },
        { name: "磁盘IOPS（读）", value: "disk.iops.read", unit: "IOPS" },
        { name: "磁盘IOPS（写）", value: "disk.iops.write", unit: "IOPS" },
        { name: "磁盘吞吐量（读）", value: "disk.throughput.red", unit: "Kbps" },
        { name: "磁盘吞吐量（写）", value: "disk.throughput.write", unit: "Kbps" },
        { name: "出网流量", value: "nic.throughput.red", unit: "Mbps" },
        { name: "入网流量", value: "nic.throughput.write", unit: "Mbps" }
      ],
      metrics_id: "",
      active: ""
    };
  },
  created() {
    this.FnGetRulesList();
  },
  methods: {
    handleCurrentChange(v) {
      this.currentPage = v;
      this.FnGetRulesList();
    },
    search() {
      this.selectData = Object.assign(
        {},
        { name: this.keyword },
        { in_active: this.active },
        { product_type: this.productDefault },
        { metrics_id: this.metrics_id }
      );
      this.FnGetRulesList();
    },
    async FnGetRulesList() {
      this.list = [];
      let reqData = {
        name: this.selectData.name,
        in_active: this.selectData.in_active,
        product_type: this.selectData.product_type,
        metrics_id: this.selectData.metrics_id,
        page_number: this.currentPage,
        page_size: this.page_size
      };
      let res = await getRulesList(reqData);
      if (res.code === "Success") {
        this.list_num = res.total_count;
        this.list = res.data;
        let groupList = [];
        let keyList = [];
        this.list.forEach(item => {
          keyList = Object.keys(item.notification_group);
          groupList = [];
          keyList.forEach(m => {
            groupList.push({
              id: m,
              name: item.notification_group[m]
            });
          });
          item.group_list = groupList;
        });
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    FnOperate(type, item) {
      this.operateData = this.operateList[type];
      this.operateData.type = type;
      this.operateData.id = item.id;
      this.msgParam.visible = true;
    },
    async FnCheckLinkGroup(item) {
      this.checkParam.title = "报警联系人";
      this.checkData = item.group_list;
      this.checkParam.visible = true;
    },
    async FnGetLinkInstance(item) {
      this.checkInstancesParam.visible = true;
      let reqData = {
        rule_id: item.id,
        page_number: 1,
        page_size: 100000
      };
      let res = await linkInstance(reqData);
      if (res.code === "Success") {
        this.instance_list = res.data;
      } else {
        Message({ message: res.msg, type: "error" });
      }
    },
    onDialog() {
      this.FnGetRulesList();
    },
    async FnSyncInstance () {
      let instancesInfo = [];
      this.instance_list.forEach(item => {
        instancesInfo.push({
          instance_id: item.instance_id,
          instance_type: item.product_type
        })
      })
      let reqData = {
        instance_info: instancesInfo
      }
      let res = await syncInstance(reqData);
      if (res.code === 'Success') {
        let keys = [];
        let data = res.data;
        data.length > 0 && data.forEach(item => {
          keys.push(item.instance_id);
        })
        let sign = -1;
        this.instance_list.forEach(item => {
          sign = keys.indexOf(item.instance_id)
          if (sign > -1) {
            this.$set(item, 'name', data[sign].name)
          }
        })
      } else {
        Message({message: res.msg, type: 'error'});
      }
    }
  }
};
</script>

<style>

</style>
