import Fetch from '../http'
export default {
    get_contact_group_info_list(reqData) {
        return Fetch({
            method: 'post',
            url: '/alarm/contact/list_all_group_info',
            data: reqData,
            loadingStatus: true
        })
    },
    get_contact_group_list(reqData) {
        return Fetch({
            method: 'post',
            url: '/alarm/contact/list_group',
            data: reqData
        })
    },
    get_contact_list(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/list_contact',
            data: reqData
        })
    },
    add_contact_group(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/add_group',
            data: reqData
        })
    },
    update_contact_group(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/update_group',
            data: reqData
        })
    },
}

// 删除组内联系人
export function delete_group_contact(reqData) {
    return Fetch({
        method: 'POST',
        url: '/alarm/contact/delete_group_contact',
        data: reqData
    })
}
// 删除联系组
export function delete_contact_group(reqData){
    return Fetch({
        method: 'POST',
        url: '/alarm/contact/delete_group',
        data: reqData
    })
}
