<template>
  <el-dialog v-if="data" :title="param.title" :visible.sync="visible" :before-close="closeDialog" class="cds-dialog">
    <div label-width="120">
        <div class="dialog-body">
            <div v-for="item in data" :key="item.group_id">
                <p>{{item.name}}</p>
                <el-table class="table--transparent" :data="item.list">
                    <el-table-column label="姓名" prop="contact_name"></el-table-column>
                    <el-table-column label="邮箱" prop="email"></el-table-column>
                </el-table>
            </div>
        </div>
    </div>
    <div class="dialog-footer" slot="footer">
        <el-button class="cds-button" type="primary" @click="closeDialog">确定</el-button>
    </div>
  </el-dialog>
</template>
<script>
import ElDialog from "@/components/dialog";
import Message from "element-ui";
export default {
  components: { ElDialog },
  props: {
    data: {
      type: Array,
      default: []
    },
    param: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {};
  },
  computed: {
    visible: {
      set: function(val) {},
      get: function() {
        return this.param.visible;
      }
    }
  },
  methods: {
    closeDialog() {
      this.visible = false;
      this.$emit("close");
    }
  }
};
</script>

