import Fetch from '../http'
export default {
    get_contact_person_list(reqData) {
        return Fetch({
            method: 'post',
            url: '/alarm/contact/list_all_info',
            data: reqData,
            loadingStatus: true
        })
    },
    get_contact_group_list(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/list_group',
            data: reqData
        })
    },
    add_contact_person(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/add',
            data: reqData
        })
    },
    update_contact_person(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/update',
            data: reqData
        })
    },
    add_to_group(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/add_groups',
            data: reqData
        })
    },
    send_code(reqData) {
        return Fetch({
            method: 'POST',
            url: '/alarm/contact/send_code',
            data: reqData
        })
    },
    get_contact_group_info_list(reqData) {
        return Fetch({
            method: 'post',
            url: '/alarm/contact/list_all_group_info',
            data: reqData,
        })
    },
}

export function delete_contact_person(reqData){
    return Fetch({
        method: 'POST',
        url: '/alarm/contact/delete',
        data: reqData
    })
}
