<template>
  <el-dialog v-if="data" :title="data.title" :visible.sync="visible" :before-close="closeDialog" class="cds-dialog msg-dialog">
    <div label-width="120">
        <div class="dialog-body">
            <el-row class="table-layout">
              <el-col>
                  <span style="font-size:50px;color:#F74020;"><i class="el-icon-warning"></i></span>
              </el-col>
              <el-col>
                  <div>{{data.msg}}</div>
                  <div class="remark m-top10">{{data.msg2}}</div>
              </el-col>
            </el-row>
        </div>
    </div>
    <div class="dialog-footer" slot="footer">
        <el-button class="cds-button" type="primary" @click="confirm">确定</el-button>
        <el-button class="cds-button" @click="closeDialog">取消</el-button>
    </div>
  </el-dialog>
</template>
<script>
import ElDialog from "@/components/dialog";
import {
  activeRules,
  inactiveRules,
  deleteRules
} from "@/service/alarm/alarm_rules";
import { delete_contact_person } from "@/service/alarm/alarm_contact_person"
import {
    delete_contact_group,
    delete_group_contact
} from "@/service/alarm/alarm_contact_group"
import { Message } from "element-ui";
export default {
  components: { ElDialog },
  props: {
    data: {
      type: Object,
      default: {}
    },
    param: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {};
  },
  computed: {
    visible: {
      set: function(val) {},
      get: function() {
        //   if (this.param.visible) {

        //   }
        return this.param.visible;
      }
    }
  },
  methods: {
    closeDialog() {
      this.visible = false;
      this.$emit("close");
    },
    async confirm() {
      let ids = [];
      ids.push(this.data.id);
      let reqData = {
        ids: ids
      };
      let person_reqData = {
        contact_ids: [this.data.contact_ids]
      };
      let group_reqData = {
        group_ids:[this.data.group_ids]
      };
      let group_person_reqData = {
        group_id: this.data.group_id,
        contact_ids: [this.data.contact_ids]
      };
      let res = null;
      switch (this.data.type) {
        case "inactive":
          res = await inactiveRules(reqData);
          break;
        case "active":
          res = await activeRules(reqData);
          break;
        case "delete":
          res = await deleteRules(reqData);
          break;
        case "delete_person":
          res = await delete_contact_person(person_reqData);  // 删除联系人
          break;
        case "delete_group":
          res = await delete_contact_group(group_reqData);   // 删除联系组
          break;
        case "delete_group_person":
          res = await delete_group_contact(group_person_reqData);   // 删除组内联系人
          break;
        case "delete_one_person":
          this.closeDialog();   // 联系组内只有一个人
          break;
        default:
          break;
      }
      if (res) {
        if (res.code === "Success") {
          this.$emit('dialogFn');
          Message({ message: res.msg, type: "success" });
          this.closeDialog();
        } else {
          this.closeDialog();
          Message({ message: res.msg, type: "error" });
        }
      }
    }
  }
};
</script>

<style>
.msg-dialog .el-dialog{
   width: 500px;
}
.msg-dialog .el-dialog .el-row.table-layout .el-col:first-child{
  width: 80px;
  min-width: 80px;
}
</style>
